/* organizations template colors and thier css variables

var(--black-30)        --> rgba(0,0,0,0.3)
var(--black-20)        --> rgba(0,0,0,0.2)
var(--black-10)        --> rgba(0,0,0,0.1)

var(--dark-gray)       --> rgba(96,96,96,1)      or   #606060
var(--text-gray)       --> rgba(112,112,112,1)   or   #707070
var(--light-gray)      --> rgba(221,221,221,1)   or   #ddd

var(--white)           --> rgba(255,255,255,1)   or   #fff
var(--white-85)        --> rgba(255,255,255,0.85)
*/

.Organization {
  min-height: 100vh;
  padding-bottom: 3rem;
}

.Organization .Wrapper {
  background-color: var(--white);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin-top: -4.5rem;
  padding-top: 4rem;
  z-index: 1;
}

.Organization .LinkButton {
  margin: 0 auto;
  width: 85%;
}

.Organization .org-link {
  background-color: var(--dark-green);
  border-radius: var(--border-radius);
  box-shadow: var(--black-box-shadow);
  color: var(--white);
}

.MainImage {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid var(--bright-green);
  box-shadow: 0.2rem 0.3rem 2.5rem rgba(0,0,0,0.5);
  height: 6.5rem;
  margin: -20% auto 0;
  overflow: hidden;
  position: relative;
  text-align: center;
  top: -1rem;
  width: 6.5rem;
}

.MainImage .fa-camera {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}
.Header .hero-image {
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right center;
  display: flex;
  justify-content: center;
  height: 29vh;
  position: relative;
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
  z-index: -1;
}

.Header .hero-image h2 {
  color: var(--white);
  letter-spacing: 2px;
  margin-top: 2rem;
  text-shadow: 0.2rem 0.2rem 0.5rem rgba(0,0,0,1);
}

.Header .fa-image {
  align-self: center;
  color: var(--text-gray);
  font-size: 2.75rem;
}

.TextSection {
  margin-bottom: 2rem;
}

.TextSection p {
  color: var(--text-gray);
  font-size: 1.2rem;
  line-height: 1.6rem;
  /* letter-spacing: 0.5px; */
  padding: 0 1rem;
}

.OrgDetails {
  margin: 2rem auto;
  /* width: 93%; */
}

.OrgDetails a {
  color: var(--dark-green);
  font-size: 1.2rem;
  display: block;
  margin-top: 1.5rem;
  padding: 0 0.65rem;
}

.CauseTile {
  flex: 0 0 33%;
  padding: 0.5rem;
}

.CauseTile a {
  text-decoration: none;
}

.CauseTile .wrapper {
  background-size: cover;
  background-position: center;
  border-radius: var(--border-radius);
  box-shadow: var(--mini-box-shadow);
  height: 6rem;
  position: relative;
}

.CauseTile a h4 {
  color: var(--dark-gray);
  font-size: 0.8rem;
  margin: 0.5rem 0;
}

.CauseTile .progress {
  background-color: var(--light-green);
  border-radius: var(--border-radius);
  border-top-left-radius: 0px;
  bottom: 0;
  height: 26%;
  overflow: visible;
  position: absolute;
}

.CauseTile a h5 {
  background-color: var(--black-20);
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  bottom: 0;
  color: var(--white);
  left: 0;
  letter-spacing: 0.03rem;
  margin-top: -0.3rem;
  padding: 0.3rem 0;
  position: absolute;
  width: 100%;
}

.CauseTile .wrapper img {
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  height: 60%;
  width: 100%;
}

/******************************************
   Featured CauseTile components styling
******************************************/

.CauseTile.featured {
  flex: 0 0 90%;
  margin: 0 0.2rem;
}

.CauseTile.featured .wrapper {
  height: 25vh;
}

.CauseTile.featured .progress {
  height: 18%;
}

.CauseTile.featured a h5 {
  font-size: 1.25rem;
  height: 13.5%;
}

.CauseTile.featured a h4 {
  font-size: 1.35rem;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.CauseTile.featured img {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1rem;
}

.Slider {
  display: flex;
  flex-wrap: nowrap;
  margin: 1rem auto;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-align: center;
}

.OrgCauses {
  margin: 3rem 0;
  text-align: center;
}

.OrgCauses h3 {
  color: var(--dark-green);
  font-size: 1.5rem;
  text-transform: uppercase;
}

.OrgCauses .cause-wrapper {
  display: flex;
  flex-wrap: nowrap;
  margin: 1rem auto;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

